@media screen and (max-width: 1023px) {
  /*GLOBAL*/
  .sectionHeader {
    padding: 0 0 30px 0;
    margin: 0;
    font-size: 7vw;
  }
  /*NAVBAR*/
  #navBar {
    height: 50px;
  }
  .navLink {
    font-size: 2.5vw;
  }
  .logoWrap {
    margin-right: 12vw;
  }
  .dropdown {
    margin-right: 45vw;
  }

  /*HEADER*/
  .mainLogo {
    padding: 0px 60px;
    font-size: 25vw;
    text-align: center;
  }
  /*ABOUT*/
  #about {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 90px;
  }
  .victoria {
    float: none;
    display: block;
    width: 60%;
  }
  .aboutText {
    float: none;
    font-size: 1.25rem;
    width: 70%;
    margin: 0;
    display: block;
    text-align: center;
  }

  /*FAQ*/
  #faq {
    padding: 0;
    padding: 60px;
  }

  /*LOCATION*/
  #location {
    padding:70px 0;
    margin:0;
  }

  /*Gallery*/
  #galleryContainer {
    padding-top: 90px;
  }

  /*Contact*/
  #contact {
    padding-top: 90px;
  }

  /*POLICIES STYLES */
  #policies {
    text-align: center;
  }
}
